html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 9rem !important;
}

footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 8rem;
  text-align: center;
}
.site-title {
  font-size: 18px !important;
}

.my-nav {
  background-color: #7cedab !important;
}
.member-thumbnail {
  height: 2.25rem;
  border-radius: 9999px;
  margin-bottom: 0.25rem;
}

.member-info {
  font-size: 12px;
  width: 85px;
}

footer {
  color: #333;
  background-color: #e1e1e1;
}
.quiz-form-container {
  max-width: 70%;
  margin: 0 3rem;
}

.my-button, .uk-button-default:focus {
  background-color: #7cebad;
  border-color: transparent;
}

@media screen and (max-width: 840px) {
  .quiz-form-container {
    max-width: 100%;
    margin: 0 1rem;
  }
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

h3 {
    background-color: #7CEDAB;
    text-align: center;

}

.signupWrapper{
max-width: 640px;
margin: 40px auto;
background-color: #E1E1E1;
margin-bottom: 40%;
}

.stateSel {
    margin-left: 20%;

}

#loginBtn {
    background-color: #7CEDAB;
    border: none;
    font-weight: 400;
    color: #FAF9F8;
    letter-spacing: 1.2px;
}

#signupBtn {
    background-color: #7CEDAB;
    border: none;
    font-weight: 400;
    color: #FAF9F8;
    letter-spacing: 1.2px; 
    margin-left: 30px;
}

#signupBtn:hover, #loginBtn:hover {
    background-color: #E1E1E1;
}


